import { useState } from "react"



export const Select = ({ className, options, accessor, onChange, name }) => {
    const [selectedValue, setSelectedValue] = useState(null)

    const handleChange = ({ target }) => {
        const { value } = target
        setSelectedValue(value)
        if (onChange) onChange(name, value)
    }

    return (
        <div className={`${className} shop-dropdown`}>
            <select
                className="space-mr--20"
                onChange={handleChange}
                value={'Matan'}>
                {(options || []).map(option => <option key={option._id} value={option[accessor]}>{option[accessor]}</option>)}
            </select>
        </div>
    )
}