import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { ShopProducts } from "../../components";
import { getSortedProducts } from "../../helpers/product";
import { ConstTranslate } from "../../constants/translate";
import { Select } from "../../components/Core-Components/Select/Select";

const Shop = () => {
  const [sortType, setSortType] = useState('');
  const [sortValue, setSortValue] = useState('');
  const [finalSortedProducts, setFinalSortedProducts] = useState([]);
  const { products } = useSelector((state) => state.product);
  const { jobTitles, companies } = useSelector((state) => state.categoriesManager);

  const getSortParams = (sortType, sortValue) => {
    setSortType(sortType);
    setSortValue(sortValue);
  }

  const handleChange = (name, value) => {
  }


  useEffect(() => {
    let sortedProducts = getSortedProducts(products, sortType, sortValue);
    setFinalSortedProducts(sortedProducts);
  }, [products, sortType, sortValue]);


  return (
    <div className="body-wrapper space-pb--120">
      {/*====================  shop header ====================*/}
      <div className="breadcrumb-area space-pt--5 space-pb--25">
        <div className="container">

          <div className="row align-items-center">
            <div className="col-5">
              <h1 className="page-title">{ConstTranslate.SORT_BY}</h1>
            </div>
            <Select className={'col-7 text-end'} />
          </div>

          <div className="flex align-center justify-space">
            <div className="col-3">
              <h1 className="page-title">{ConstTranslate.FILTER_BY}</h1>
            </div>
            <Select options={companies} accessor={"company"} name={"company"} onChange={handleChange} />
            <Select options={jobTitles} accessor={"title"} name={"jobTitle"} onChange={handleChange} />
          </div>

        </div>
      </div>
      {/*====================  End of shop header  ====================*/}

      {/* shop products */}
      {finalSortedProducts && <ShopProducts products={finalSortedProducts} />}
    </div>
  );
}

export default Shop;